import React, {useState, useEffect, useContext} from 'react'
import { stateContext } from '../contexts/context'
import { useResource } from 'react-request-hook'
import { useNavigation } from 'react-navi'

export default function CreateToDoList () {

  const [ title, setTitle] = useState('');
  const [ description, setDescription] = useState('');
  const [ dateCreated, setDateCreated] = useState('');
  const [ complete, setComplete] = useState(false);
  const [ dateComplete, setDateComplete] = useState('');
  const { state, dispatch } = useContext(stateContext);
  const { user } = state;
  const navigation = useNavigation()

  const [todo, createToDo ] = useResource(({ title, description, dateCreated, complete, dateComplete, author }) => ({
    url: '/todo',
    method: 'post',
    headers: {"Authorization": `${state.user.access_token}`},
    data: { title, description, dateCreated, complete, dateComplete, author }
  }))

  function checkComplete(){
    if (!complete){
      var todayDate = new Date().toISOString().slice(0, 10);
      setComplete(current => !current);
      setDateComplete(todayDate);
    }else{
      setComplete(current => !current);
      setDateComplete('');
    }
  }

  useEffect( () => {
  }, [complete]);

  function handleToDo(){
    createToDo({title, description, dateCreated, complete, dateComplete, author: user.username})
  }

  useEffect(() => {
    if (todo && todo.data) {
      // console.log("CreateToDo date in API:" + todo.data.dateComplete);
      // console.log("CreateToDo date:" + dateComplete);
      dispatch({ type: 'CREATE_TODO', title: title, description: description, dateCreated: dateCreated, complete: complete, dateComplete: dateComplete, id: todo.data.id, author: user.username})
      // navigation.navigate('/todo/')
    }
  }, [todo])

  return (
    <form onSubmit={e => {e.preventDefault(); handleToDo();} }> 
      <div class="input-group col-lg-12 mb-0">
        <input id="title" name="title" placeholder="title" 
          defaultValue={title} onChange={(e)=>setTitle(e.target.value)}/>
        <input id="description" description="title" placeholder="description" 
          defaultValue={description} onChange={(e)=>setDescription(e.target.value)}/>
        <input id="dateCreated" type="date" description="dateCreated" placeholder="dateCreated"
          defaultValue={dateCreated} onChange={(e)=>setDateCreated(e.target.value)}/>
        <input id="complete" type="checkbox" description="complete" 
          defaultValue={complete} onChange={checkComplete}/>
        <input id="dateComplete" type="date" description="dateComplete" placeholder="dateComplete" 
          defaultValue={dateComplete}/>
        <input id="add" type="submit" value="Add" /> 
      </div>
    </form>
  )
}
