import React, { useEffect, useContext } from 'react'
import { useResource } from 'react-request-hook'
import { stateContext } from '../contexts/context'
import ToDoList from '../to-do/ToDoList'
import DetailsProfile from '../about/DetailsProfile'

export default function HomePage () {

  const { state, dispatch } = useContext(stateContext)
  const { user } = state;
  const [ todo, getToDos ] = useResource(() => ({
    url: '/todo',
    headers: {"Authorization": `${state.user.access_token}`},
    method: 'get'
  }))

  useEffect(() =>{
    getToDos()
  }, [state.user.access_token])

  useEffect(() => {
    if (todo && todo.data && todo.isLoading === false) {
      // console.log("todo:" + todo);
      // console.log("todo-data: " + JSON.stringify(todo.data));
      dispatch({ type: 'FETCH_TODOS', todos: todo.data.todo })
      }
    }, [todo])

    const { data, isLoading } = todo;
    if (user) {
      return (
        <>
        {isLoading && 'loading...'} <ToDoList />
        </>
      )
    } else {
      return <> </>
    }
  } 