import {useState, useReducer, useEffect } from 'react';
import { useResource } from 'react-request-hook';
import { mount, route } from 'navi';
import { Router, View } from 'react-navi';
import { stateContext } from './contexts/context';
import { Container } from 'react-bootstrap';

import HomePage from './pages/HomePage';
import UserBar from './user/UserBar';
import NavigationBar from './pages/NavigationBar';
import DetailsAbout from './about/DetailsAbout';
import DetailsProfile from './about/DetailsProfile';
import Profile from './about/Profile';
import appReducer from './reducers';
import ToDoList from './to-do/ToDoList';


function App () {

  const [ state, dispatch ] = useReducer(appReducer, { user: {}, todos: [] })
  
  // const {user} = state;

  const routes = mount({
    '/': route({ view: <HomePage />}),
    '/userbar': route({ view: <UserBar />}),
    '/about': route({ view: <DetailsAbout />}),
    '/profile': route({ view: <DetailsProfile />}),
    '/todo/:id': route(req=> {
      return {view: <ToDoList id={req.params.id} />}
    })
  })

  return (
    <div>
      <stateContext.Provider value={{state: state, dispatch: dispatch }}>
        <Router routes={routes}>
          <Container>
            <NavigationBar />
            <hr />
            <View />
          </Container>
        </Router>
      </stateContext.Provider>
    </div>
  );
}

export default App;
