import React, {useState, useContext, useEffect} from 'react'
import 'bootstrap/dist/css/bootstrap.min.css';
import { stateContext } from '../contexts/context'
import { useResource } from 'react-request-hook'
import { Card, Button } from 'react-bootstrap'
import { useNavigation } from 'react-navi'

function ToDo ({title,description,dateCreated,complete,dateComplete,id}) {

  const [ completeTask, setCompleteTask] = useState('');
  const [ dateCompleteTask, setDateCompleteTask] = useState('');
  const { state, dispatch } = useContext(stateContext);
  const navigation = useNavigation()
  
  const [ deletedTodo , deleteToDo ] = useResource((id) => ({
    url: `/todo/${id}`,
    headers: {"Authorization": `${state.user.access_token}`},
    method: 'delete',
    data: {id}
  }))

  function checkComplete(){
    if (!completeTask){
      var todayDate = new Date().toISOString().slice(0, 10);
      setCompleteTask('checked');
      setDateCompleteTask(todayDate);
    }else{
      setCompleteTask('');
      setDateCompleteTask('');
    }
  }
  
  useEffect(() => {
    // console.log("Delete todo START useEffect: " + id);
    if (deletedTodo && deletedTodo.data && deletedTodo.isLoading === false) {
      // console.log("Delete todo IN useEffect 1: " + deletedTodo.data.id);
      // console.log("Delete todo IN useEffect 2: " + id);
      var deleteID;
      if (typeof(deleteToDo.data) == 'undefined') {
        deleteID = id;
      }else{
        deleteID = deleteToDo.data.id;
      }
      // console.log("Delete todo IN useEffect 3: " + deleteID);
      dispatch({type: 'DELETE_TODO', id: deleteID});
    }
  },[deletedTodo])


  return (
      // <form onSubmit={(e)=> {handleToDo()}}> 
      <form>
        <div class="input-group col-lg-12 mb-0">
          {/* <input type="text" value={id} disabled="disabled"/> */}
          <input type="text" defaultValue={title} disabled="disabled"/>
          <input type="text" defaultValue={description} disabled="disabled" />
          <input type="date" defaultValue={dateCreated} disabled="disabled"/>
          <input type="checkbox" onChange={checkComplete} checked={completeTask}/>
          <input type="date" defaultValue={dateCompleteTask}/>
          {/* <input id="add" type="submit" value="Delete" />  */}
          <Button variant="link" onClick={(e) => {deleteToDo(id)}}>Delete</Button>
        </div>
      </form>
    )
  }


export default React.memo(ToDo);